@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
* {
  margin: 0;
  padding: 0; 
  -ms-overflow-style: none;
}

::-webkit-scrollbar {
  width: 1em;
}

::-webkit-scrollbar-track {
  background-color: #101010;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(39, 36, 36);
}

.about {
height: 100%;
width: 100%;
position: relative;
}

.about_hello {
  height: 100%;
  font-size: 2rem;
  padding: 2rem 0 2rem 2rem;
  color: white;
}

.about_name {
  height: 100%;
  font-size: 6rem;
  padding: 2rem 0;
  text-align: center;
  color: white;
}

.about_intro {
  height: 100%;
  padding: 2rem 2rem 2rem 0;
  text-align: right;
  color: white;
}

.about_contact {
  height: 100%;
  padding: 0rem 0 2rem 2rem;
  text-align: left;
  color: white;
}

@media only screen and (min-width: 741px) {
  .about {
    width: 80%;
  }

  .about_hello {
    font-size: 3rem;
  }
  
  .about_name {
    font-size: 8rem;
  }

  .about_intro {
    font-size: 1.3rem;
  }

  .about_contact {
    font-size: 1.2rem;
  }
}

.skills {
  height: 100%;
  width: 100%;
}

.skill_title {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  text-align: center;
  color: #959da5;
  opacity: 0.9;
}

.skill_items {
  font-family: "Open Sans", sans-serif;
  text-align: center;
  font-size: 1.8rem;
  color: white;
  line-height: 3.2rem;
  padding: 2rem 0;
}

@media only screen and (min-width: 741px) {
  .skill_title {
    font-size: 8rem;
  }

  .skill_items {
    font-size: 2.2rem;
    line-height: 3.8rem;
  }
}

.exp {
  height: 200%;
  width: 100%;
}

.exp_title {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  text-align: center;
  color: #959da5;
  opacity: 0.9;
}

.exp_items {
  font-family: "Open Sans", sans-serif;
  color: white;
  padding: 10rem 0 0;
}

.exp_item_title {
  text-align: center;
  font-family: "Lato", sans-serif;
  padding: 2rem 0 1.5rem;
  font-size: 1.6rem;
}

.exp_item_desc {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  padding: 1rem 0 2rem;
  font-size: 1.18rem;
  line-height: 1.6rem;
}



@media only screen and (min-width: 741px) {
  .exp_title {
    font-size: 8rem;
  }

  .exp_item_title {
    font-size: 2.4rem
  }

  .exp_item_desc {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  .exp_paper {
    width: 50rem;
  }
}
.prj {
  height: 100%;
  width: 100%;
}

.prj_title {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  text-align: center;
  color: #959da5;
  opacity: 1;
}

.prj_items {
  font-family: "Open Sans", sans-serif;
  color: white;
  padding: 8rem 0;
}

@media only screen and (min-width: 741px) {
  .prj_title {
    font-size: 8rem;
  }
}

.blog {
  height: 100%;
  width: 100%;
}

.blog_title {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  text-align: center;
  opacity: 1;
}

.blog_items {
  font-family: "Open Sans", sans-serif;
  color: white;
  padding: 5rem 0 0;
}


@media only screen and (min-width: 741px) {
  .blog_title {
    font-size: 8rem;
  }
}

